import React, {useEffect} from 'react'
import {redirect} from 'react-router-dom'
import { browserHistory } from 'react-router';
import Home from './Home.js'
import mainImg from '../img/mainImg.png'

import '../css/Main_style.css'
export default function Header() {

    const signOut = e =>{
        e.preventDefault();
        sessionStorage.clear()
        window.location.reload(true)
        
    }

    function readAll(){
        var flag=0;
var bool = true;
var no_rows = 0
var frame_data;
var current_row;
var img= document.getElementById('sun_image');
img.src = "https://i.ibb.co/XxqS6kk/Pngtree-pizza-logo-logo-vector-1063555-4.png"
var page = document.getElementById('page')
img.style.transtion = "all 0.5s";
img.addEventListener('mouseover', () => {img.style.content = "url('https://snazzy-maps-cdn.azureedge.net/assets/marker-4dcd83c2-edfc-49db-a308-4cf4798f4c78.gif')"})
img.addEventListener('mouseleave', ()=> {img.style.content = "url('https://i.ibb.co/XxqS6kk/Pngtree-pizza-logo-logo-vector-1063555-4.png')"})
var items = document.getElementsByClassName("img_ref")
var decors = items[0]
var frames = items[1]
var polaroids = items[2]
var memories = items[3]
decors.addEventListener('mouseover', ()=>{img.style.height = "0px"; window.setTimeout(()=>{img.style.height = "7vw"; img.style.content = "url('https://i.ibb.co/QfBnv9x/Png-Item-54864.png')"}, 250); flag=1})
frames.addEventListener('mouseover', ()=>{img.style.height = "0px"; window.setTimeout(()=>{img.style.height = "7vw"; img.style.content = "url('https://i.ibb.co/wwf403s/pngwing-com-1.png')"}, 250); flag=1})
polaroids.addEventListener('mouseover', ()=>{img.style.height = "0px"; window.setTimeout(()=>{img.style.height = "7vw"; img.style.content = "url('https://i.ibb.co/Wvkxw8Q/pngimg-com-sandwich-PNG61.png')"}, 250); flag=1})
memories.addEventListener('mouseover', ()=>{img.style.height = "0px"; window.setTimeout(()=>{img.style.height = "7vw"; img.style.content = "url('https://i.ibb.co/k0TNDjx/favpng-orange-juice-animation-table-glass-drink.png')"}, 250); flag=1})
for(let i=0; i<items.length; i++)
{
    document.getElementById("navi").addEventListener('mouseleave', ()=>{if(flag==1){img.style.height = "0px"; window.setTimeout(()=>{img.style.height = "7vw"; img.style.content = "url('https://i.ibb.co/qrQTLXk/Pngtree-pizza-logo-logo-vector-1063555-2.png')"}, 250);flag=0;}})
}

    }

    useEffect(()=>{readAll()}, [])

    
    return (
        <div>
            <div className="Header">
                <span><img className="sun_image" id="sun_image"  /></span>Vinay's
            <div className="title">Pizza</div>
                <h6 id="userName">Hey there, {sessionStorage.getItem("currentUserName")}</h6>
                <a href="/favs" id="fav_btn">Favs</a>
                <a href="/cart" id="cart_btn">Cart</a>
                <div id="imgCont"><img id="userImg" src={sessionStorage.getItem("currentUserPic")}  />
                    <ul id="imgMenu">
                        <li onClick={signOut}>Signout</li>
                    </ul>
                </div>
               
            </div>
            <nav className="navigation" id="navi">
    <a href="#" className="img_ref" id="Decors_ref">Burgers</a>
    <a href="#" className="img_ref" id="Frames_ref">Pizzas</a>
    <a href="#" className="img_ref" id="Polaroids_ref">Sanwiches</a>
    <a href="#" className="img_ref" id="Memories_ref">Cool Drinks</a>
    </nav>
        </div>
    )
}
