import React, {useEffect} from 'react'
import Data from '../dataStore/frames.json'
import {useNavigate} from 'react-router-dom'
import {useParams} from 'react-router-dom'
import { initializeApp } from "firebase/app";
export default function Order() {
   
    var {dumps} = useParams(); 
    dumps = JSON.parse(dumps)
    var currentObject = sessionStorage.getItem("currentObject")
    currentObject = JSON.parse(currentObject)
    var navigate = new useNavigate()

    function onCancel(){
        alert("Order Cancelled")
        navigate("/", {replace : true})
    }

    function onReceive(){
        alert("Enjoy the meal..!!")
        navigate("/", {replace : true})
    }

    useEffect(()=>{document.getElementById("mainFlex").style.display = "none"})

  
    return (
        <div>
            <br /> <br /><br/>
            <div className="Dumps container">
                <div className="row">
                    <div className="col-md-3"><img id="" style={{width:"15vw"}} src={currentObject.images[0]} /></div>
                    <div className="col-md-3" style={{float:"left"}}><br /><h3>{currentObject.name}</h3><h5>{currentObject.desc}</h5>
                    <h6>Price: {dumps.price} rs</h6><br /></div>
                </div>
                <br/>
                <button style={{backgroundColor : "#BB3E00"}}  onClick={()=>{onCancel()}}>Cancel Order</button>&nbsp;<button style={{backgroundColor : "#5F8D37"}} onClick={()=>{onReceive()}}>Order Received</button> 
            </div>
            <br /> <br /><br/><br /> <br /><br/><br /> <br /><br/>
            
        </div>
    )
}
