import React, {useState, useEffect} from 'react'
import Data from '../dataStore/frames.json'
import {useParams, useNavigate} from "react-router-dom"
import { getDatabase, set, ref } from "firebase/database";
import { initializeApp } from "firebase/app";

export default function Dumplings() {
    const app = initializeApp({
        apiKey: "AIzaSyCLtKfYS-DakArniaKoFbCX0tkrWaxp70Q",
  authDomain: "vinays-pizza.firebaseapp.com",
  databaseURL: "https://vinays-pizza-default-rtdb.firebaseio.com",
  projectId: "vinays-pizza",
  storageBucket: "vinays-pizza.appspot.com",
  messagingSenderId: "988813984271",
  appId: "1:988813984271:web:73fd489441cdbbd957597a"
      })

    const db = getDatabase(app)

    let { id } = useParams();
    const currentItem = id;
    const currentObject = Data.filter(value =>{return value.id == currentItem})[0]
    sessionStorage.setItem("currentObject", JSON.stringify(currentObject))
    const navigate = new useNavigate()
    var [price, setPrice] = useState(currentObject.price)
    var [itemCount, setItemCount] = useState(1)
    var fakedumps;
    var [dumps, setDumps] = useState({
        Items : itemCount,
        price : price,
        base : "Stuffed Crust",
        sauce : "White Garlic",
        cheese : "Mozzarella",
        name : "",
        email : "",
        address : "",
        city : "",
        phone : 0,
        pincode : 0,
        Itemid: id

    })

    

    function incrementPrice(){
        fakedumps = dumps
        fakedumps["Items"] = itemCount+1
        fakedumps["price"] = price + currentObject.price
        setDumps(fakedumps)
        setPrice(price + currentObject.price)
        setItemCount(itemCount + 1)
        
        // alert(price)
    }
    function decrementPrice(){
        fakedumps = dumps
        if(itemCount > 1){
            fakedumps["Items"] = itemCount-1
            fakedumps["price"] = price-currentObject.price
            setDumps(fakedumps)
            setPrice(price - currentObject.price)
            setItemCount(itemCount - 1)
            
        }
        
    }

    function eventRadio(e){
        e.preventDefault()
        fakedumps = dumps
        fakedumps[e.target.name] = e.target.value
        setDumps(fakedumps)
        // alert(JSON.stringify(dumps))
    }

    function initializeRadio(){
       var objs = document.getElementsByClassName("mainbase")
        for(let i=0; i< objs.length; i++){
            objs[i].checked = "true"
        }
    }

    function addressHandler(e){
        e.preventDefault()
        fakedumps = dumps
        fakedumps[e.target.name] = e.target.value
        setDumps(fakedumps)
    }

    function submitHandler(){
        alert("Order Received...!!")
        navigate(`/dumps/order/${JSON.stringify(dumps)}`, {replace : true})
        set(ref(db, 'orders/' + sessionStorage.getItem("currentUserId")), dumps);
        
    }

    useEffect(()=>{initializeRadio()},[])

    return (
        <div>
            <br /> <br /><br/>
            <div className="Dumps container">
                <div className="row">
                    <div className="col-md-3"><img id="" style={{width:"15vw"}} src={currentObject.images[0]} /></div>
                    <div className="col-md-3" style={{float:"left"}}><br /><h3>{currentObject.name}</h3><h5>{currentObject.desc}</h5>
                    <h6>Price: {price} rs</h6><br /><div style={{width:"100%",display:"flex"}}>Items: &nbsp;<button onClick={()=>{decrementPrice()}}>-</button><div style={{textAlign : "center", border : "solid 0.5px black", width:"2vw"}}>{itemCount}</div><button onClick={()=>{incrementPrice()}}>+</button></div></div>
                </div>
                <br/>
                <h5>Select your favourite Pizza Base</h5>
                <div className="row">
                    <div className="col-md-3" onChange={eventRadio}>
                        
                        <input type="radio" className="mainbase" value="Stuffed Crust" name="base" />Stuffed Crust<br/>
                        <input type="radio"  value="Thin Crust" name="base" />Thin Crust<br/>
                        <input type="radio" value="Thich Crust" name="base" />Thick Crust<br/>
                        <input type="radio" value="Cracker Crust" name="base" />Cracker Crust<br/>
                        <input type="radio" value="Flatbread Crust" name="base" />Flatbread Crust<br/>
                    </div>
                </div>
                <br/>
                <h5>Select your favourite Pizza Sauce</h5>
                <div className="row">
                    <div className="col-md-3" onChange={eventRadio}>
                        
                        <input type="radio" className="mainbase" value="White Garlic" name="sauce" />White Garlic Sauce<br/>
                        <input type="radio"  value="Garlic Ranch" name="sauce" />Garlic Ranch Sauce<br/>
                        <input type="radio" value="Marinara Sauce" name="sauce" />Marinara Sauce<br/>
                        <input type="radio" value="Pesto Sauce" name="sauce" />Pesto Sauce<br/>
                        <input type="radio" value="Hummus Sauce" name="sauce" />Hummus Sauce<br/>
                    </div>
                </div>
                <br/>
                <h5>Select your favourite Cheese</h5>
                <div className="row">
                    <div className="col-md-3" onChange={eventRadio}>
                        
                        <input type="radio" className="mainbase" value="Mozzarella" name="cheese" />Mozzarella Cheese<br/>
                        <input type="radio"  value="Aged Havarti" name="cheese" />Aged Havarti Cheese<br/>
                        <input type="radio" value="Gorgonzola" name="cheese" />Gorgonzola Cheese<br/>
                        <input type="radio" value="Provolone" name="cheese" />Provolone Cheese<br/>
                        <input type="radio" value="Pecorino-Romano" name="cheese" />Pecorino-Romano Cheese<br/>
                    </div>
                </div>
                <br/>
                <h5>Enter Your Details</h5>
                <div className="row">
                    <form className="col-md-3" onChange={addressHandler} >
                        <input type="text" placeholder="Enter Your Name" name="name" /><br/>
                        <input type="email" placeholder="Enter Your Mail" name="email" /><br/>
                        <input type="number" placeholder="Mobile Number" name="phone" /><br/>
                        <input type="text" placeholder="Address" name="address" /><br/>
                        <input type="text" placeholder="City" name="city" /><br/>
                        <input type="number" placeholder="Pincode" name="pincode" /><br/>
                        <br/>
            <button id="pay_btn" onClick={submitHandler}>Pay on Delivery </button>
                    </form>
                </div>
                
                <br/><br/>
            </div>
        </div>
    )
}
